/* Стили для меню */
.catalog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #e9ecef;
  position: relative;
}

.app-logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #0056b3;
  letter-spacing: 0.5px;
}

.menu-toggle {
  cursor: pointer;
  z-index: 100;
}

.hamburger-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 24px;
  height: 18px;
}

.hamburger-icon span {
  display: block;
  height: 2px;
  width: 100%;
  background-color: #343a40;
  transition: all 0.3s ease;
}

.menu-toggle.active .hamburger-icon span:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.menu-toggle.active .hamburger-icon span:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active .hamburger-icon span:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

.menu-dropdown {
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: white;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  min-width: 200px;
  max-width: 300px;
  overflow: hidden;
}

.menu-item {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  cursor: pointer;
  transition: background-color 0.2s;
  position: relative;
}

.menu-item:hover {
  background-color: #f8f9fa;
}

.menu-icon {
  margin-right: 10px;
  font-size: 1.1rem;
  width: 20px;
  text-align: center;
}

.menu-text {
  flex: 1;
}

.menu-separator {
  height: 1px;
  background-color: #e9ecef;
  margin: 5px 0;
}

.menu-dropdown-parent {
  position: relative;
}

.menu-dropdown-content {
  display: none;
  position: absolute;
  right: 100%;
  top: 0;
  background-color: white;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  min-width: 150px;
}

.menu-dropdown-parent:hover .menu-dropdown-content {
  display: block;
}

.menu-subitem {
  padding: 10px 16px;
  cursor: pointer;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
}

.menu-subitem:hover {
  background-color: #f8f9fa;
}

.menu-subitem.active {
  background-color: #e9ecef;
  font-weight: bold;
}

.mode-icon {
  margin-right: 10px;
  font-size: 1.1rem;
}

/* Адаптивные стили для десктопа */
@media (min-width: 992px) {
  .menu-dropdown {
    top: 60px;
    right: 20px;
    width: 250px;
  }
  
  .menu-dropdown-content {
    right: 100%;
    top: 0;
  }
}

/* Адаптивные стили для планшетов */
@media (min-width: 768px) and (max-width: 991px) {
  .menu-dropdown {
    top: 60px;
    right: 20px;
    width: 230px;
  }
  
  .menu-dropdown-content {
    right: 100%;
    top: 0;
  }
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 767px) {
  .menu-dropdown {
    top: 60px;
    right: 10px;
    left: 10px;
    width: auto;
  }
  
  .menu-dropdown-content {
    position: static;
    box-shadow: none;
    border: none;
    border-top: 1px solid #e9ecef;
    border-radius: 0;
    margin-top: 5px;
    padding-left: 30px;
  }
  
  .menu-dropdown-parent:hover .menu-dropdown-content {
    display: block;
  }
  
  .menu-item {
    flex-wrap: wrap;
  }
}
